:root {
	--padding: calc(var(--safe-area-inset-bottom) + var(--tabbar_height));
	--bottom: calc(var(--tabbar_height) + var(--safe-area-inset-bottom));
}
.Tabbar {
	animation: tabbarAnimation 0.7s ease;
}
.Panel__in {
	padding-bottom: var(--padding)!important;
}
.Gallery__bullets {
	bottom: calc(var(--safe-area-inset-bottom) + 10px);
}
.Div, .Header {
	-webkit-user-select: none;
	user-select: none;
}
.Epic .FixedLayout--bottom {
	padding-bottom: var(--bottom)!important;
}
.Alert__btn {
	cursor: pointer;
}
.Tabbar--l-horizontal .TabbarItem__text {
	animation: textAnimation 0.2s ease-out;
	font-size: 16px;
}
.Avatar__img, .Banner--ios .Banner__in {
	border-radius: 32px!important;
}
.Avatar__shadow {
	display: none;
}
.Banner__header {
	margin-top: 12px!important;
	font-size: 1.6em!important;
}
.Banner__subheader {
	margin-top: 10px!important;
	font-size: 1em!important;
	line-height: normal!important;
}
#BannerBackground {
	background-size: 400% 400%;
	animation: BannerAnimation 15s ease infinite;
}
#Apple .Banner__subheader, #Apple { color: #ffffff }
#Spotify .Banner__subheader, #Spotify { color: #ffffff; }
#Yandex .Banner__subheader, #Yandex { color: #ffffff; }

.SongCell {
	animation: songAdd 0.3s ease;
}

@keyframes songAdd {
	0% {
	  transform:  translate(0px, -10px);
	}
	75% {
		transform:  translate(0px, 5px);
	  }
	100% {
	  transform:  translate(0px, 0px);
	}
  }

@keyframes textAnimation {
  0% {
    opacity: 0.1;
    transform:  translate(0px, 50px);
  }
  100% {
    opacity: 1;
    transform:  translate(0px, 0px);
  }
}
@keyframes tabbarAnimation {
	0% {
	  transform:  translate(0px, 100px);
	}
	100% {
	  transform:  translate(0px, 0px);
	}
  }
@keyframes BannerAnimation {
	0% {
		background-position: 0% 50%;
	}
	75% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}
@media screen and (max-width: 333px) {
	.IntroImg {
		width: 250px!important;
		height: 250px!important;
	}
	.Button--sz-l .Button__content, .Banner__subheader {
		font-size: 13px!important;
	}
	.Banner__header {
		font-size: 23px!important;
	}
}
@media screen and (max-width: 313px) {
	.Button--sz-l .Button__content, .Banner__subheader {
		font-size: 11px!important;
	}
	.Banner__header {
		font-size: 21px!important;
	}
}
@media screen and (max-width: 298px) {
	.Button--sz-l .Button__content, .Banner__subheader {
		font-size: 9px!important;
	}
	.Banner__header {
		font-size: 19px!important;
	}
}
@media screen and (max-width: 283px) {
	.Button--sz-l .Button__content, .Banner__subheader {
		font-size: 8.2px!important;
	}
	.Banner__header {
		font-size: 18.2px!important;
	}
}